.greetingForm {
    padding-left: 70px;
}

.inputAndButtonContainer {
    display: flex;
    gap: 20px;
}

.error {
    color: red;
    padding: 5px 0;
}

.input {
    width: 372px;
    height: 36px;
    border: 1px solid #D1D1D1
}

.input:focus {
}

.errorInput {
    border: 1px solid red
}

.button {
    background: rgba(0, 102, 204, 1);
    color: #FFFFFF;
    opacity: 0.5;
    width: 78px;
    height: 36px;
    padding: 5px 24px 5px 24px;
    border-radius: 3px;

}

.button:focus {
    opacity: 1;
}

.button:active {

    opacity: 1;
}

.button:disabled {
    opacity: 0.5;
}

.text {
    color: #00000080;
    padding: 5px 0;
}

.greeting {
    color: #000000;
    font-size: 14px;
    font-weight: 500;
}
